<template>
  <div class="v-popup-edit">
    <div class="v-popup_header_edit">
      <span class="v-popup_header_text">{{ editTask }}</span>
    </div>
    <div class="v-popup_content">
      <slot></slot>
    </div>
    <div class="v-popup-footer">
      <button class="btn-add-card" @click="saveTask">{{ saveBtnName }}</button>
    </div>
  </div>
</template>

<script>
export default {
  name: "v-popup-edit-task",
  state: {
    textInput: '',
    textDate: '',
  },
  props: {
    editTask: {
      type: String,
      default: ''
    },
    saveBtnName: {
      type: String,
      default: ''
    },
  },
  methods: {
    showPopupEditTask() {
      this.isPopupVisible = true
    },
    saveTask() {
      this.$emit('saveTask')
    }
  }
}
</script>

<style lang="scss">
.v-popup-edit {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  padding: 20px;
  background: lightgray;
  width: 400px;
  border-radius: 10px;
  &_header_text {
    font-size: var(--font-size-16);
    font-weight: bold;
  }
  &_header_edit {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &_content {
    margin: 10px 0 10px 0;
    border: 2px solid darkgrey;
    border-radius: 10px;
    padding: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background: darkgrey;
  }
  .v-popup-footer {
    display: flex;
    align-items: center;
    justify-content: right;
  }
  .btn-add-card, .btn-close-modal {
    padding: 8px;
    cursor: pointer;
    border-radius: 10px;
    border: 2px solid darkgrey;
    background: none;
  }
  .btn-add-card:enabled {
    background: green;
    color: white;
    border: 2px solid green;
  }
  .btn-close-modal:hover {
    background: red;
    color: white;
    border: 2px solid red;
  }
  .material-icons {
    padding: 5px;
    cursor: pointer;
  }
}
</style>
